body {
  margin: 0;
}

.container_header {
  display: flex;
  flex-direction: row;
  align-items: baseline;
  align-self: flex-start;

  margin: 30px;
}

.title {
  margin-left: 1vh;
  font-size: 20px;
  font-weight: bold;
  align-self: center;
}

/* configuracoes da logo */
.editImage {
  min-height: 70px;
  max-height: 70px;
}

.container_form {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  justify-content: center;
  align-items: center;
}

.container_intern_form {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  max-width: 1200px;
  justify-content: center;
}

.input_text {
  width: 90%;
  min-width: 90%;
  height: 57px;
}

.button {
  height: 57px;
  width: 12vw;
}

.span_error {
  margin: 5px 0 5px 2px;
  color: red;
  font-weight: bold;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 10px;
}

.container_repo {
  background-color: #e5e5e5;
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 5px;
}

.image_repo {
  height: 15px;
  margin: 2px;
}

.profile_pic {
  width: 30vh;
  margin: 5px;
  margin-bottom: 10px;
  max-width: 200px;
  align-self: center;
  justify-self: center;
}

.description {
  display: flex;
  flex-direction: column;
  font-family: sans-serif, Arial, Helvetica;
}

span {
  margin-bottom: 5px;
}

.container_explanation {
  margin: 50px 30px 30px 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
  max-width: 1200px;
}

.pic_1 {
  height: 30vh;
  max-height: 300px;
}

.intern_container_explanation {
  width: 100%;

  display: flex;
  flex-direction: column;
}

.span_text {
  margin: 10px;
}

.pic_2 {
  width: 70px;
  margin: 20px;
  align-self: center;
}

@media (max-width: 576px) {
  .container_explanation {
    flex-direction: column;
  }
  .intern_container_explanation {
    flex-direction: row;
  }
  .pic_1 {
    margin-top: 20px;
    order: 3;
  }
}

.Home {
  background-color: #f5f5f5;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.data {
  margin-top: 30px;
  display: flex;
  flex-direction: row;
  width: 100%;
  max-width: 1200px;
  justify-content: center;
  align-items: center;
}

@media (max-width: 767px) {
  .data {
    flex-direction: column;
  }
  .repos_container {
    margin-top: 20px;
  }
}

.data_container {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.repos_container {
  display: flex;
  flex-direction: column;
  min-width: 100%;
}

.created {
  font-size: 10px;
  justify-self: flex-end;
}

.github_icon {
  width: 15px;
  align-self: baseline;
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
  align-self: center;
  justify-self: center;
}

